import React from 'react'
import './main.css'
import 'bootstrap/css/bootstrap.css'
import Button from '@material-ui/core/Button'
import DrawerBar from './Drawer'
class ActionBar extends React.Component{
    state = {
        open:false
    }
    handleState=event=>{
        this.state.open =event
        this.setState(this.state)
    }
    
    render(){
        return (
        <div className="navbar navbar-primary bg-light">
                <a href="#" className="navbar-brand text-theme"><b>kahani.com</b></a>
                <Button onClick={()=>{this.handleState(true)}} variant="outlined" color="primary"><b>login</b></Button>
                <DrawerBar data={this.state.open} onHandleState={this.handleState}/>
        </div>        
        )
    }
}
export default ActionBar
